.settings-toggle {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 40px;
    width: 88px;
    background-color: rgba(159, 164, 169, .2);
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1) inset;
}
.settings-toggle:hover {
    cursor: pointer;
}

.settings-toggle-icon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border-radius: 6px;
    position: absolute;
    margin: 0px 4px;
    transition: .3s;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    z-index: 1;
    background: rgba(0, 0, 0, 0.02);
    
